a{
  color: white;
  background-color: transparent;
}
img{
  position:relative;
  top: 0;
}

.Container{
  height: 80vh;
  width:90%;
  background-color:#222222;
  margin:5vw;
  border-radius: 50px;
  display:flex;
  justify-content: space-between;
  align-items: flex-start;
}

.ContainerInfo{
  background-color:#222222;
  height:80%;
  width:30%;
  padding-top: 10vh;
  padding-left: 0;
}

.ContainerImage{
  background-color:transparent;
  position: relative;
  height:100%;
  width:70%;
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.ContainerImage:hover .overlay {
  height: 60%;
}

.overlay {
  position: absolute;
  bottom: 10vh;
  left: 5vw;
  right: 0;
  background-color: transparent;
  overflow: hidden;
  width: 70%;
  height: 0;
  transition: .5s ease;
}
.image{
  display: block;
  width: 95%;
  height: auto;
}
.imageOpacityOn{
  opacity:0.1;
  display:flex;
  justify-content: space-between;
  align-items: center;
}

.appiceImg{
  width:30%;
  height:auto;
}

.button{
  width: 30%;
  height:10%;
  margin-top:1vh;
  border: 2px solid white;
  background-color: transparent ;
  color: white;
}
@media only screen and (max-width: 850px) {
  .Container{
    height: 50vh;
    width:95vw;
    margin:5vw;
    border-radius: 50px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  
  .ContainerInfo{
    width:100%;
    height:auto;
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
  }
  
  .ContainerImage{
    position: relative;
    height:100%;
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 2%;
  }
  .ContainerImage:hover .overlay {
    height:85%;
    width:85%
  }
  
  .overlay {
    position: absolute;
    bottom: 0vh;
    left: 5vw;
    right: 0;
    background-color: transparent;
    overflow: hidden;
    width: 90%;
    height: 0;
    transition: .5s ease;
  }
  .image{
    width: 95%;
    height: auto;
  }
  .imageOpacityOn{
    opacity:0.1;
    display:flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .appiceImg{
    width:30%;
    height:auto;
  }


  
}