* {
  margin: 0;
  padding: 0;
  background-color: #000000;
}

a{
  color: white
}
img{
  position:relative;
  top: 0;
}

.App {
  width:99%;
  text-align: center;
  background-color: #000000;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Body{
width:80vw;
background-color: black;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

.Menu{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 5vw;
  padding-right: 5vw;
}

.Item{
  margin-right: 2vw;
}

.Intro{
  height:80vh;
  width:99vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.Personal{
  width:60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 10vw;
}
.IntroImg{
  width:40%;
}

.Footer{
  height:20vh;
  width:90%;
  background-color: #0d1115;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left:5vw;
}

.Icons{
  display:flex;
  width:10vw;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.image{
  display: block;
  width: 100%;
  height: auto;
}

.profile{
  width: 40vw;
  height: auto;
}

@media only screen and (max-width: 850px) {
  .App {
    width:100vw;
    text-align: center;
    background-color: #000000;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top:10vh
  }
  
  .Body{
  width:79vw;
  background-color:#000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
  
  .Menu{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding-left: 5vw;
    padding-right: 5vw;
  }
  
  .Item{
    margin-right: 2vw;
  }
  
  .Intro{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  
  .Personal{
    width:99vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .IntroImg{
    width:100%;
  }
  
  .Footer{
    height:20vh;
    width:90%;
    background-color: #0d1115;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left:5vw;
  }
  
  .Icons{
    display:flex;
    width:10vw;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  
  .image{
    width: 100%;
    height: auto;
  }
  
  .profile{
    width: 95vw;
    height: auto;
  }
}
